<template>
<div>

    <section class="content-header">
      <h1>Add User</h1>
    </section>
    <section class="content">
      <div class="card box-warning">
        <div class="box-body">
          <div class="tabssection">
            <ul class="nav nav-tabs">
              <li class="active"><a data-toggle="tab" href="#menu1" class="disabled">User Information</a></li>
              <li><a data-toggle="tab" href="#menu2" class="disabled">Account Information</a></li>
              <li><a data-toggle="tab" href="#menu3" class="disabled" >User Settings</a></li>
            </ul>
            <div class="tab-content">
              <div id="menu1" class="tab-pane fade in active">
                <form  @submit.prevent="next_step('frmStep1')" data-vv-scope="frmStep1">
                <div class="row">
                  <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                    <div class="form-group">
                      <label>Organization</label>
                      <select class="form-control" v-model="frm_user.organization" v-validate="'required'"  :class="{ 'error': errors.organization, 'error': verrors.has('organization'),'haveValue': frm_user.organization }" data-vv-name="organization" data-vv-scope="frmStep1">
                        <option value="United Scrap Metal">United Scrap Metal</option>
                        <option value="Others">Others</option>
                      </select>
                      <p v-if="verrors.has('frmStep1.organization')" class="errormsg"> {{ verrors.first('frmStep1.organization') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                    <div class="form-group">
                      <label>Full Name </label>
                      <input type="text" class="form-control" placeholder="Enter Full Name ..." v-model="frm_user.name"  v-validate="'required'"  :class="{ 'error': errors.name, 'error': verrors.has('name'),'haveValue': frm_user.name }" data-vv-name="name" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.name')" class="errormsg">  {{ verrors.first('frmStep1.name') }}</p>
                      
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                    <div class="form-group">
                      <label>Address 1</label> 
                      <input type="text" class="form-control" placeholder="Enter Address 1 ..." v-model="frm_user.address_1"  v-validate="'required'"  :class="{ 'error': errors.address_1, 'error': verrors.has('address_1'),'haveValue': frm_user.address_1 }" data-vv-name="address_1" data-vv-scope="frmStep1" data-vv-as="Address 1">
                      <p v-if="verrors.has('frmStep1.address_1')" class="errormsg">  {{ verrors.first('frmStep1.address_1') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-6 col-lg-6">
                    <div class="form-group">
                      <label>Address 2 <span>(optional)</span> </label>
                       <input type="text" class="form-control" placeholder="Enter Address 2 ..." v-model="frm_user.address_2"  v-validate="''"  :class="{ 'error': errors.address_2, 'error': verrors.has('address_2'),'haveValue': frm_user.address_2 }" data-vv-name="address_2" data-vv-scope="frmStep1" data-vv-as="Address 2">
                      <p v-if="verrors.has('frmStep1.address_2')" class="errormsg">  {{ verrors.first('frmStep1.address_2') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>City </label>
                       <input type="text" class="form-control" placeholder="Enter City ..." v-model="frm_user.city"  v-validate="'required'"  :class="{ 'error': errors.city, 'error': verrors.has('city'),'haveValue': frm_user.city }" data-vv-name="city" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.city')" class="errormsg">  {{ verrors.first('frmStep1.city') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>State </label>
                      <input type="text" class="form-control" placeholder="Enter State ..." v-model="frm_user.state"  v-validate="'required'"  :class="{ 'error': errors.state, 'error': verrors.has('state'),'haveValue': frm_user.state }" data-vv-name="state" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.state')" class="errormsg">  {{ verrors.first('frmStep1.state') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>Zip </label>
                     <input type="text" class="form-control" placeholder="Enter Zip ..." v-model="frm_user.zip_code"  v-validate="'required'"  :class="{ 'error': errors.zip_code, 'error': verrors.has('zip_code'),'haveValue': frm_user.zip_code }" data-vv-name="zip_code" data-vv-scope="frmStep1" data-vv-as="zip code">
                      <p v-if="verrors.has('frmStep1.zip_code')" class="errormsg">  {{ verrors.first('frmStep1.zip_code') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>Country </label> 
                      <input type="text" class="form-control" placeholder="Enter Country ..." v-model="frm_user.country"  v-validate="'required'"  :class="{ 'error': errors.country, 'error': verrors.has('country'),'haveValue': frm_user.country }" data-vv-name="country" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.country')" class="errormsg">  {{ verrors.first('frmStep1.country') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>Phone </label>
                     <input type="text" class="form-control" placeholder="Enter Phone ..." v-model="frm_user.phone"  v-validate="'required'"  :class="{ 'error': errors.phone, 'error': verrors.has('phone'),'haveValue': frm_user.phone }" data-vv-name="phone" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.phone')" class="errormsg">  {{ verrors.first('frmStep1.phone') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>Email </label>
                      <input type="text" class="form-control" placeholder="Enter Email ..." v-model="frm_user.email"  v-validate="'required|email'"  :class="{ 'error': errors.email, 'error': verrors.has('email'),'haveValue': frm_user.email }" data-vv-name="email" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.email')" class="errormsg">  {{ verrors.first('frmStep1.email') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>Fax <span> (optional)</span> </label>
                       <input type="text" class="form-control" placeholder="Enter Fax ..." v-model="frm_user.fax"  v-validate="''"  :class="{ 'error': errors.fax, 'error': verrors.has('fax'),'haveValue': frm_user.fax }" data-vv-name="fax" data-vv-scope="frmStep1">
                      <p v-if="verrors.has('frmStep1.fax')" class="errormsg">  {{ verrors.first('frmStep1.fax') }}</p>
                    </div>
                  </div>
                  <div class="col-xs-12 col-sm-12 col-md-3 col-lg-3">
                    <div class="form-group">
                      <label>User Type </label>
                      <select class="form-control" v-model="frm_user.type" v-validate="'required'"  :class="{ 'error': errors.type, 'error': verrors.has('type'),'haveValue': frm_user.type }" data-vv-name="type" data-vv-scope="frmStep1">
                      <option value="">Choose user type</option>
                      <template v-if="frm_user.organization=='United Scrap Metal'"> 
                        <option value="Admin">Admin</option>
                        <option value="Finance Team">Finance Team</option>
                        <option value="Transportation Team"> Transportation Team</option>
                        <option value="Sales Team ">Sales Team </option>
                        <option value="CEC">CEC </option>
                        </template>
                        <template v-else> 
                        <option value="Primary">Primary</option>
                        <option value="Finance">Finance</option>
                        <option value="Transportation">Transportation </option>
                      </template>
                      </select>
                      <p v-if="verrors.has('frmStep1.type')" class="errormsg"> {{ verrors.first('frmStep1.type') }}</p>
                    </div>
                  </div>
                </div>
                <div class="box-footer">
                  <div class="text-center">
                    <button type="submit" class="btn link1">Next</button>
                  </div>
                </div>
                </form>
              </div>
              <div id="menu2" class="tab-pane fade">
                <form  @submit.prevent="next_step('frmStep2')" data-vv-scope="frmStep2"> 
                <div class="tablesection">
                  <div class="table-responsive">
                    <table class="table table-striped table-hover">
                      <thead>
                        <tr>
                          <th style="text-align: center;" width="70">Sl No.</th>
                          <th>Account No</th>
                          <th width="100">Action</th>
                        </tr>
                      </thead> 
                      <tbody> 
                        <tr v-for="(item,ix) in frm_user.account_info" :key="'up'+ix">
                          <td align="center">{{ix+1}}</td>
                          <td><input v-model="item.account_no" type="text" v-validate="'required'" :name="'account_no'+ix" data-vv-scope="frmStep2"  data-vv-as="account no"/>
                          <p v-if="verrors.has('frmStep2.account_no'+ix)" class="errormsg"> {{ verrors.first('frmStep2.account_no'+ix) }}</p>
                          </td>
                           <td>
                        <button type="button" class="btn btn-danger" v-if="ix>0" @click="frm_user.account_info.splice(ix, 1)"><i aria-hidden="true" class="fa fa-trash-o"></i></button> 
                        <button type="button" class="btn btn-success" v-if="frm_user.account_info.length==ix+1" @click="addInfo()"><i aria-hidden="true" class="fa fa-plus"></i></button>
                        </td>
                        </tr>
                       
                      </tbody>
                    </table>
                  </div>
                </div>
                <div class="box-footer">
                  <div class="text-center">
                    <button type="button" class="btn link2 mx-1" @click="step--">Back</button>
                    <button type="submit" class="btn link1 mx-1">Next</button>
                  </div>
                </div>
                </form>
              </div>
              <div id="menu3" class="tab-pane fade">
                <form  @submit.prevent="submit_user('submit')" data-vv-scope="frmStep2"> 
                <div class="box-header with-border bor">
                  <div class="pull-left">
                    <h3 class="box-title card-text">Permissions Granted</h3>
                  </div>
                  <div class="pull-right dropdown"> <a href="add_permission.html" class="btn link2 widthauto"> <span><i class="fa fa-pencil" aria-hidden="true"></i> </span> Add Permission</a> &nbsp;&nbsp;
                    <button class="btn link1 widthauto" type="button" data-toggle="dropdown"><i class="fa fa-cog" aria-hidden="true"></i></button>
                    <ul class="dropdown-menu">
                      <li><a href="#">HTML</a></li>
                      <li><a href="#">CSS</a></li>
                      <li><a href="#">JavaScript</a></li>
                    </ul>
                  </div>
                  <div class="clearfix"></div>
                </div>
                <div class="box-body">
                  <div class="tablesection">
                    <div class="table-responsive">
                      <table class="table table-striped table-hover">
                        <thead>
                          <tr>
                            <th style="text-align: center;" width="70">Sl No.</th>
                            <th><input type="checkbox">
                            </th>
                            <th>Permission Name</th>
                            <th>Permission Deseripton</th>
                            <th>Action</th>
                          </tr>
                        </thead>
                        <tbody>
                          <tr>
                            <td align="center">01</td>
                            <td><input type="checkbox"></td>
                            <td>-</td>
                            <td>-</td>
                            <td>-</td>
                          </tr>
                          <tr>
                            <td align="center">02</td>
                            <td><input type="checkbox"></td>
                            <td>-</td>
                            <td>-</td>
                            <td>-</td>
                          </tr>
                          <tr>
                            <td align="center">03</td>
                            <td><input type="checkbox"></td>
                            <td>-</td>
                            <td>-</td>
                            <td>-</td>
                          </tr>
                        </tbody>
                        <tfoot>
                          <tr>
                            <td align="center" colspan="5">No Permissions Granted</td>
                          </tr>
                        </tfoot>
                      </table>
                    </div>
                  </div>
                  <div class="box-footer">
                    <div class="text-center">
                      <button type="button" class="btn link2 mx-1" @click="step--">Back</button>
                      <button type="submit" class="btn link1 mx-1">Submit</button>
                    </div>
                  </div>
                </div>
            </form>

              </div>
            </div>
          </div>
        </div>
      </div>
    </section> 
  </div>
</template>

<script>
import {
    mapGetters
} from "vuex";
    export default {
      name:"UserAddEdit",
        data () {
            return {
                editmode: false,
                step:1,
                users : {}, 
                frm_user: new Form({
                  id : '',
                  organization: '',
                  name: '',
                  address_1: '',
                  address_2: '',
                  city: '',
                  state: '',
                  zip_code: '',
                  country: '',
                  phone: '',
                  email: '',
                  fax: '',
                  type: '',
                  account_info:[]
                })
            }
        },
            computed: {
        ...mapGetters(["errors"])
    },

 

        methods: {
    async addInfo() {
            this.frm_user.account_info.push({
                id:'',
                account_no:'',
            })
        },
                  async next_step(final=false) {
            this.$validator.validateAll('frmStep' + this.step).then(async (valid) => {
                if (valid) {
                    this.step++
                    if (final=='submit') {
                        this.step=1;
                        this.submit_user();
                    }
                }
            })
        },



        submit_user(){
            if(this.frm_user.id>0){
              this.frm_user.put('api/user/'+this.frm_user.id)
                    .then((response) => {
                        if (response.data.success == true) {
                        this.frm_user.reset();
                        Toast.fire({
                            icon: 'success',
                            title: response.data.message
                        });                         
                        this.$router.push('/users').catch(() => {});
                        }
                    })
            }else{
              this.frm_user.post('api/user')
                    .then((data) => {
                        this.frm_user.reset();
                        $('a[href="#menu1"]').tab('show');
                        this.frm_user.account_info=[]
                        this.addInfo()
                        Toast.fire({
                            icon: 'success',
                            title: data.data.message
                        });
                          
                    })
          }
        },


            getResults(page = 1) {

                  this.$Progress.start();
                  
                  axios.get('api/user?page=' + page).then(({ data }) => (this.users = data.data));

                  this.$Progress.finish();
            },
            updateUser(){
                this.$Progress.start();
                // console.log('Editing data');
                this.form.put('api/user/'+this.form.id)
                .then((response) => {
                    // success
                    $('#addNew').modal('hide');
                    Toast.fire({
                      icon: 'success',
                      title: response.data.message
                    });
                    this.$Progress.finish();
                        //  Fire.$emit('AfterCreate');

                    this.loadUsers();
                })
                .catch(() => {
                    this.$Progress.fail();
                });

            },
            editModal(user){
                this.editmode = true;
                this.form.reset();
                $('#addNew').modal('show');
                this.form.fill(user);
            },
            newModal(){
                this.editmode = false;
                this.form.reset();
                $('#addNew').modal('show');
            },
            deleteUser(id){
                Swal.fire({
                    title: 'Are you sure?',
                    text: "You won't be able to revert this!",
                    showCancelButton: true,
                    confirmButtonColor: '#d33',
                    cancelButtonColor: '#3085d6',
                    confirmButtonText: 'Yes, delete it!'
                    }).then((result) => {

                        // Send request to the server
                         if (result.value) {
                                this.form.delete('api/user/'+id).then(()=>{
                                        Swal.fire(
                                        'Deleted!',
                                        'Your file has been deleted.',
                                        'success'
                                        );
                                    // Fire.$emit('AfterCreate');
                                    this.loadUsers();
                                }).catch((data)=> {
                                  Swal.fire("Failed!", data.message, "warning");
                              });
                         }
                    })
            },
          loadUsers(){
            this.$Progress.start();

            if(this.$gate.isAdmin()){
              axios.get("api/user").then(({ data }) => (this.users = data.data));
            }

            this.$Progress.finish();
          },
          
          createUser(){

              this.form.post('api/user')
              .then((response)=>{
                  $('#addNew').modal('hide');

                  Toast.fire({
                        icon: 'success',
                        title: response.data.message
                  });

                  this.$Progress.finish();
                  this.loadUsers();

              })
              .catch(()=>{

                  Toast.fire({
                      icon: 'error',
                      title: 'Some error occured! Please try again'
                  });
              })
          }

        },
        mounted() {
            if(this.$route.params.id){
              this.frm_user.id=this.$route.params.id;

            axios.get("api/user/"+this.$route.params.id)
                  .then((res) => {                     
                      this.frm_user.fill(res.data.data)                
                  })

            }
        },
        created() {

            this.$Progress.start();
            this.addInfo();
            this.$Progress.finish();
        },
            watch: {
              async step(n ) {
                $('a[href="#menu'+n+'"]').tab('show');
              },
                    async 'frm_user.organization'(n){
        console.log(n)
        if(n=='United Scrap Metal'){
          if(!['Admin','Finance Team','Transportation Team','Sales Team','CEC'].includes(this.frm_user.type)){
            this.frm_user.type=''
          }
      }else{
         if(!['Primary','Finance','Transportation'].includes(this.frm_user.type)){
            this.frm_user.type=''
          }
      }
      }
            }
    }
</script>
